import React from 'react';
import styled from "styled-components";
import { Dialog } from 'primereact/dialog';
import { InputText } from "primereact/inputtext";
import { inject, observer } from "mobx-react";
import { SNIPPETS } from "consts/styles";
import { PrimaryButton } from "components/public/buttons";
import { Dropdown } from "primereact/dropdown";
import { InputNumber } from "primereact/inputnumber";

const SubscriptionModal = inject('subscrMW', 'uiStore', 'userStore' )(observer(({ subscrMW, uiStore, userStore }) => {

    return (
        <Modal
            onHide={() => {
                subscrMW.hideActiveSubscrModal()
            }}
            visible={subscrMW.subscrModalState === subscrMW.SUBSCR_MODAL_STATES.SUBSCRIPTIONS}
            header={<Header>Замовлення підписки</Header>}
        >
            <div className="p-inputgroup">
                <InputText
                    className={`${!subscrMW.isNameValid && 'p-invalid '}`}
                    type="text"
                    onInput={subscrMW.setName}
                    placeholder="Як вас звати?"
                />
            </div>
            {uiStore.cities.length && <Dropdown
                className="city"
                optionLabel="name"
                optionValue="id"
                options={uiStore.cities}
                value={ userStore.user.city.id || uiStore.cities[0].id }
                onChange={ e => {
                    userStore.user.setCity(uiStore.cities.find(city => city.id === e.value));
                }}
            />}
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className="bx bx-phone" />
                </span>
                <InputText
                    className={!subscrMW.isPhoneValid && 'p-invalid '}
                    type="phone"
                    onInput={subscrMW.setPhone}
                    placeholder="Номер телефону"
                />
            </div>
            <PrimaryButton onClick={subscrMW.subscribe} disabled={!subscrMW.isSubscriptionComplete} >Замовити</PrimaryButton>
        </Modal>
    )
}));

const remontTypes = [
    { name: 'Будь-який'},
    { name: 'Євроремонт'},
    { name: 'Мінімалізм'},
    { name: 'Класичний'}
]

const SubscriptionPersonalModal = inject('subscrMW', 'uiStore', 'userStore' )(observer(({ subscrMW, uiStore, userStore }) => {

    return (
        <Modal
            onHide={() => {
                subscrMW.hideActiveSubscrModal()
            }}
            visible={subscrMW.subscrModalState === subscrMW.SUBSCR_MODAL_STATES.PERSONAL}
            header={<Header>Замовлення персонального підбору</Header>}
        >
            <div className="p-inputgroup">
                <InputText
                    className={`${!subscrMW.isNameValid && 'p-invalid '}`}
                    type="text"
                    onInput={subscrMW.setName}
                    placeholder="Як вас звати?"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className="bx bx-phone" />
                </span>
                <InputText
                    className={!subscrMW.isPhoneValid && 'p-invalid '}
                    type="phone"
                    onInput={subscrMW.setPhone}
                    placeholder="Номер телефону"
                />
            </div>
            {uiStore.cities.length && <Dropdown
                className="city"
                optionLabel="name"
                optionValue="id"
                options={uiStore.cities}
                value={ userStore.user.city.id || uiStore.cities[0].id }
                onChange={ e => {
                    userStore.user.setCity(uiStore.cities.find(city => city.id === e.value));
                }}
            />}
            <div className="p-inputgroup">
                <InputText
                    className={`${!subscrMW.isNameValid && 'p-invalid '}`}
                    type="text"
                    onInput={subscrMW.setRegion}
                    placeholder="Район"
                    value={subscrMW.region}
                />
            </div>
            <div className="p-inputgroup">
                <InputNumber
                    min={subscrMW.rooms && 1}
                    value={subscrMW.rooms}
                    showButtons
                    onValueChange={subscrMW.setRooms}
                    placeholder="Кількість кімнат"
                />
            </div>
            <Dropdown
                options={remontTypes}
                value={subscrMW.building}
                placeholder="Ремонт"
                onChange={subscrMW.setBuilding}
                optionLabel="name"
                optionValue="name"
            />
            <div className="p-inputgroup">
                <InputNumber
                    min={subscrMW.peoples && 1}
                    showButtons
                    onChange={subscrMW.setPeoples}
                    placeholder="Скільки людей буде проживати"
                    value={subscrMW.peoples}

                />
            </div>
            <div className="p-inputgroup">
                <InputNumber
                    min={0}
                    showButtons
                    step={100}
                    value={subscrMW.money}
                    onValueChange={subscrMW.setMoney}
                    placeholder="Максимальний бюджет"
                />
            </div>
            <PrimaryButton onClick={subscrMW.subscribePersonal} disabled={!subscrMW.isSubscriptionComplete} >Замовити</PrimaryButton>
        </Modal>
    )
}));

export {
    SubscriptionModal,
    SubscriptionPersonalModal
};

const Modal = styled(Dialog)`
  color: ${props => props.theme.colors.text.important};
  .p-dialog-header-close {
    margin-left: 20px;
    ${SNIPPETS.noOutline};
  }
  
  .p-inputgroup {
    margin: 10px auto;
    input {
      ${SNIPPETS.noOutline};
    }
  }
  button {
    width: 100%;
  }
  
  .footer {
    display: flex;  
  }
  .names {
    display: grid;
    input {${SNIPPETS.noOutline};}
  }
  
  .p-dropdown {
    width: 100%;
  }
  .p-inputnumber {
    width: 100%;
  }
`;

const Header = styled.h3`
  font-size: 2rem;
  line-height: 1.1;
  font-weight: 700;
  margin: 30px 0 0 0;
  display: flex;
  color: ${props => props.theme.colors.text.important};
  img {
    height: 2rem;
    margin: auto;
    padding-left: 10px;
  }
`;
